<template>
    <div style="width: 100%;height: 100%;">
        <div align="right">
            <el-input
                    placeholder=""
                    v-model="caseName"
                    style="width: 250px;margin: 0px;padding: 0px;"
                    size="small"
                    prefix-icon="el-icon-search"
                    clearable>
            </el-input>
            <el-button type="primary" size="small" style="margin-left: 10px" @click="searchMarker"></el-button>
        </div>
        <!---->
        <div style="width: 100%;height: 96.4%;overflow: hidden;border:1px solid gray" id="map_container">
        </div>
    </div>
</template>
<script>
import axios from 'axios'
    export default {
        name: '',
        data() {
            return {
                caseName: '',
                map: null,
                e: {lng: null, lat:null, text: null}
            }
        },
        methods: {
            searchMarker() {
                let __this = this;
                axios.get("/map/pitchByCaseName",{params:{caseName:__this.caseName}}).then(resp => {
                   var data=resp.data
                   data.forEach(element => {
                        __this.pointMarker(element);
                   });
                   
                }, resp => {
                    if (resp.response.status == 403) {
                    
                    }
 
                });
            },
            pointMarker(data) {
                let __this = this;
                let point = new BMap.Point(data.lng, data.lat); // 
                let marker = new BMap.Marker(point);
                let opts = {
                    width: 210, // 
                    height: 125, // 
                    title: '<h4>' + data.name + '</h4>', // 
                    enableMessage: true, //
                    message: ""
                };
                let infoWindow = new BMap.InfoWindow("<br>:" + data.lng + "<br>:" + data.lat, opts); // 
                __this.map.openInfoWindow(infoWindow, point); //
                __this.map.addOverlay(marker);
            },
            createMaker(e) {
              let __this = this;
              __this.pointMarker(e);
            },
        },
        created() {
            let __this = this;
        },
        mounted() {
            let __this = this;
            __this.map = new BMap.Map("map_container");      //
            let point = new BMap.Point(117.32, 34.82);    // 
            __this.map.centerAndZoom(point, 13);                     // ,
            __this.map.setCurrentCity("");
            __this.map.addControl(new BMap.MapTypeControl());
            __this.map.addControl(new BMap.NavigationControl());
            __this.map.enableScrollWheelZoom();                  // 
            __this.map.enableKeyboard();                         // 
            //
            let styleOptions = {
                strokeColor: "red",    //
                fillColor: "red",      //
                strokeWeight: 1,       //
                strokeOpacity: 0.8,	   //0 - 1
                fillOpacity: 0.4,      //0 - 1
                strokeStyle: 'solid' //soliddashed
            }
            //
            let drawingManager = new BMapLib.DrawingManager(__this.map, {
                isOpen: false, //
                enableDrawingTool: true, //
                drawingToolOptions: {
                    anchor: BMAP_ANCHOR_TOP_RIGHT, //
                    offset: new BMap.Size(5, 5), //
                    drawingModes: [
                        BMAP_DRAWING_MARKER,
                        BMAP_DRAWING_CIRCLE,
                        BMAP_DRAWING_POLYLINE,
                        BMAP_DRAWING_RECTANGLE
                    ],
                    scale: 0.5 //
                },
                circleOptions: styleOptions, //
                polylineOptions: styleOptions, //
                polygonOptions: styleOptions, //
                rectangleOptions: styleOptions //
            });
            __this.map.addControl(new BMap.MapTypeControl({
                mapTypes: [
                    BMAP_NORMAL_MAP
                ]
            }));
            //
            drawingManager.addEventListener('rectanglecomplete', function (e) {

                let b = e.getBounds();

                if (typeof (window.external.SetScale) != 'undefined') {
                    window.external.SetScale(b.getSouthWest().lng, b.getSouthWest().lat, b.getNorthEast().lng, b.getNorthEast().lat);
                }
            });
            __this.createMaker(__this.e);
        },
    }
</script>
<style type="text/css">
    body, html, #map_container {
        width: 100%;
        height: 100%;
        overflow: hidden;
        margin: 0;
    }

    .anchorBL {
        display: none;
    }
</style>



